<template>
  <div class="table-dark row-submission">
      <select class="cell cell-input form-control form-control-sm" 
          placeholder="#" v-model="newStart" style="grid-column: 2 / 5">
        <option selected disabled hidden value="">N1</option>	
        <option v-for="node in nodes" :key="node.id" :value="node">{{ node.n }}</option>
      </select>
      <select class="cell cell-input form-control form-control-sm" v-model="newEnd" 
          @keyup.enter="submitElement">
        <option selected disabled hidden value="">N2</option>		
        <option v-for="node in nodes" :key="node.id" :value="node">{{ node.n }}</option>
      </select>
    <div class="cell-button"><button 
      type="button" 
      class="btn btn-outline-success submit" 
      @click="submitElement" 
    ><i class="icon-add"></i></button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ElementRowAdd',
	props: ['elements', 'nodes'],
	data() {
		return {
			newStart: '',
			newEnd: '',
		}		
	},
	methods: {
		submitElement() {
			this.$emit('submit', { start: this.newStart, end: this.newEnd })
			this.newStart = ''
			this.newEnd = ''
		},
	},
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



