<template>
  <div class="list-container">
    <div class="list-header">
      <div class="list-heading" style="grid-column: span 9">
          <div>{{ heading }} ({{ array.length }}) </div>
          <button 
            class="btn btn-outline-dark" 
            @click="$emit('expand')"
            style="margin-left: 0.5rem; "
          ><i class="icon-unfold-more"></i></button>
          <button 
              class="btn btn-outline-dark" 
              @click="$emit('collapse')"
          ><i class="icon-unfold-less"></i></button>
      </div>
      <slot name="header"></slot>
    </div>
    <transition-group name="list" tag="ul">
      <slot></slot>
    </transition-group>
  </div>
</template>

<script>
export default {
  name: 'TableArray',
	props: ['heading', 'array'],
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import "@/assets/styles/_variables.scss";

.list-container {
	background-color: white;
	display: grid; 
	grid-template-rows: min-content 1fr; 
  margin: 0;
  position: relative;
}

.list-heading {
  padding: 0.5rem;
  font-weight: 700;
  background-color: $dark-cool;
  color: white;
  border: none !important;
	display: flex;
	grid-column: 1 / end;
  button {
    border: none !important; 
    color: white !important; 
    font-weight: 700 !important;
    height: 1.6em;
    padding: 0 !important;
    width: 1.6em;
  }
}

thead, .list-header {
  background-color: $dark-cool;
  tr {
    border: none;
    padding: 0em 0.5em;
  }
}

</style>



