<template>
    <div 
				class="input-group input-group-sm"
        @mouseover="showSpinner=true"
				@mouseleave="showSpinner=false">
      <div v-if="hasSlotData" class="input-group-prepend">
        <slot></slot>
      </div>
      <input 
					type="number" 
					step="any"
					class="cell-input form-control form-control-sm" 
					v-model.number="node[inputValue]"
      />
      <div v-if="showSpinner" class="input-group-append btn-spinner">
        <button 
						class="btn btn-outline-secondary" 
						type="button" 
						@mousedown="increment(step)" 
						@mouseup="clearTiming" 
						@mouseleave="clearTiming" 
						@touchstart="increment(step)" 
						@touchend="clearTiming" 
						tabindex="-1"
        >&#x25b5;</button>
        <button 
						class="btn btn-outline-secondary" 
						type="button" 
						@mousedown="increment(-step)" 
						@mouseup="clearTiming" 
						@mouseleave="clearTiming" 
						@touchstart="increment(-step)" 
						@touchend="clearTiming" 
						tabindex="-1"
        >&#x25bf;</button>		
      </div>
  </div>
</template>

<script>
export default {
  name: 'InputNumber',
  props: {
		node: Object,
		inputValue: String,
		step: {
			type: Number,
			default: 1,
		}
	},
	data() {
		return {
			showSpinner: false,
			timeout: null,
			interval: null
		};
	},
	computed: {
  	hasSlotData() {
    	return this.$slots.default;
    }
	},
	methods: {
		increment(step) {
			this.node[this.inputValue] += step;
			this.timeout = setTimeout(() => {
				this.interval = setInterval(() => { 
					this.node[this.inputValue] += step;
				}, 25);
			}, 250);
		}, 
		clearTiming() {
			clearTimeout(this.timeout);
			clearInterval(this.interval);
		}
	},
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">

.btn-spinner {
	background-color: white; 
	display: grid !important; 
	grid-template: 50% 50% / 1.125rem !important;
	button {
		height: 100% !important;
		line-height: 1em !important;
		margin: 0 !important;
		padding: 0 !important; 
	}
	button:first-child {
		border-radius: 0 0.2rem 0 0 !important;
		margin-bottom: -1px !important; 
	}
	button:last-child {
		border-radius: 0 0 0.2rem 0 !important;
		border-top: none;
	}
}

</style>



