<template>
  <div id="canvas-settings-container">

      <transition name="slide-fade">
        <div id="canvas-settings" v-show="config.display">
          
          <div>
            <div>Zoom (scroll canvas)</div>
            <input 
                type="number" 
                class="form-control form-control-sm" 
                min="0" :max="config.scaleMax"
                step="0.001"
                v-model.number="config.scale"
                style="width: 50%"
            >
          </div>
          <div style="grid-column: span 2">
            <input type="range" class="form-control-range" 
                :min="config.scaleMin" :max="config.scaleMax" 
                step="0.001" v-model.number="config.scale"/>
          </div>

          <div>Origin offset (drag canvas)</div>
          <div>
            <input type="range" class="form-control-range" 
                min="0" :max="config.canvasW" v-model.number="config.x"/>
          </div>
          <div>
            <input type="range" class="form-control-range" style="direction: rtl"
              min="0" :max="config.canvasH" v-model.number="config.y"/>
          </div>
          
          <div>
            <div>Signifiicant figures (1-9)</div>
            <div style="width: 70%">
              <input 
                  type="number" 
                  class="form-control form-control-sm" 
                  min="1" max="9" 
                  v-model.number="config.significantFigures"
              >
            </div>
          </div>

          <div>
            <div>Diagram scale</div>
            <div style="width: 70%">
              <input 
                  type="number" 
                  class="form-control form-control-sm" 
                  step="0.1"
                  v-model.number="config.diagramScale"
              >
            </div>
          </div>

          <input-checkbox :object="config" style="grid-column-start: 1"
              :setting="'nodeLabels'" 
              :label="'Node labels'"
          ></input-checkbox>
          <input-checkbox :object="config" 
              :setting="'appliedForces'" 
              :label="'Nodal loads'"
          ></input-checkbox>
          <input-checkbox :object="config" 
              :setting="'elementLoads'" 
              :label="'Distributed loads'"
          ></input-checkbox>
          <input-checkbox :object="config" 
              :setting="'supports'" 
              :label="'Supports'"
          ></input-checkbox>
          <input-checkbox :object="config" 
              :setting="'elementForces'" 
              :label="'Axial forces'"
          ></input-checkbox>
          <input-checkbox :object="config" 
              :setting="'reactions'" 
              :label="'Reactions'"
          ></input-checkbox>
          <input-checkbox :object="config" 
              :setting="'displacements'" 
              :label="'Displacements'"
          ></input-checkbox>
          <input-checkbox :object="config" 
              :setting="'shearDiagrams'" 
              :label="'Shear diagrams'"
          ></input-checkbox>
          <input-checkbox :object="config" 
              :setting="'momentDiagrams'" 
              :label="'Moment diagrams'"
          ></input-checkbox>
          <input-checkbox :object="config" 
              :setting="'axes'" 
              :label="'Axes'"
          ></input-checkbox>
          
          <div class="input-group input-group-sm" style="width: 70%; grid-column-start: 1">
            <div class="input-group-prepend">
              <button class="btn btn-outline-light" type="button" @click="$emit('loaded')">Load</button>
            </div>
            <input type="text" class="form-control form-control-sm" v-model="config.loadValue">
          </div>

          <div class="input-group input-group-sm" style="width: 70%">
            <div class="input-group-prepend">
              <button class="btn btn-outline-light" type="button" @click="$emit('saved')">Save</button>
            </div>
            <input type="text" class="form-control form-control-sm" v-model="config.saveValue">
          </div>

          <div>Copy/paste strings to load/save structures.</div>

      </div>
    </transition>

    <div id="canvas-header">
      <div>Canvas</div>
      <button 
          class="btn btn-outline-dark" 
          @click="config.display=!config.display"
      >&#x2699;</button>
    </div>

  </div>
</template>

<script>
import InputCheckbox from './InputCheckbox'

export default {
  name: 'CanvasSettings',
  components: {
    InputCheckbox,
  },
  props: ['config'],
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
#canvas-settings {
  align-items: center;
	font-size: 0.875rem;
	background-color: #21252988;
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-gap: 0.75rem 0.5rem;
	padding: 0.5rem 1rem;
  z-index: 1;
}

#canvas-header {
	align-items: center; 
	background-color: #212529;
	margin: 0;
	display: flex; 
	font-weight: 700;
	justify-content: space-between; 
	padding: 0.5rem 0.5rem 0.5rem 1rem;
  z-index: 1;
  button {
      border: none; 
      color: white; 
      font-weight: 700;
  }
}

</style>



