<template>
  <div class="input-group-text">
    <input type="checkbox" v-model="model[val]">
  </div>
</template>

<script>
export default {
  name: 'InputNumberCheckbox',
  props: ['model', 'val'],
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
input {
  height: 100%;
}
</style>



