<template>
  <div class="table-dark row-submission">
      <input 
        type="text" 
        class="cell cell-input form-control form-control-sm" 
        v-model.number="newNodeX" 
        placeholder="X" 
        oninput="this.value = this.value.replace(/[^0-9.-]/g, '').replace(/(\\..*)\\./g, '$1')"	
        style="grid-column: 2 / 5"
      />
      <input 
        type="text" 
        class="cell cell-input form-control form-control-sm" 
        v-model.number="newNodeY" 
        @keyup.enter="submitNode" 
        placeholder="Y"
        oninput="this.value = this.value.replace(/[^0-9.-]/g, '').replace(/(\\..*)\\./g, '$1')" 
      />
      <div class="cell-button">
        <button 
          type="button" 
          class="btn btn-outline-success submit" 
          @click="submitNode" 
        ><b><i class="icon-add"></i></b></button>
      </div>
  </div>
</template>

<script>
export default {
  name: 'NodeRowAdd',
	props: ['nodes'],
	data () {
		return {
			newNodeX: '',
			newNodeY: '',
		}
	},
	methods: {
		submitNode() {
			this.$emit('submit', { 
        x: this.newNodeX === '' ? 0 : this.newNodeX, 
        y: this.newNodeY === '' ? 0 : this.newNodeY 
      });
			this.newNodeX = '';
			this.newNodeY = '';
		},
	},
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.row-submission {
  
}
</style>



